            <!-- Menu Group with Children -->
            <div v-else class="menu-group">
              <button @click="toggleMenuGroup(item.id)" :class="['nav-group-header w-full group flex items-center px-3 py-2 text-sm font-medium rounded-lg transition-colors duration-200', expandedGroups.includes(item.id) || hasActiveChild(item) ? 'bg-gray-50 text-gray-900' : 'text-gray-700 hover:bg-gray-50 hover:text-gray-900']">
                <Icon :icon="item.icon" :class="['mr-3 h-5 w-5 transition-colors duration-200', expandedGroups.includes(item.id) || hasActiveChild(item) ? 'text-gray-500' : 'text-gray-400 group-hover:text-gray-500']"/>
                <span class="flex-1 text-left">{{ item.label }}</span>
                <Icon icon="mdi:chevron-down" :class="['h-4 w-4 transition-transform duration-200', expandedGroups.includes(item.id) ? 'rotate-180' : '']"/>
              </button>

              <!-- Submenu -->
              <transition name="submenu" enter-active-class="transition-all duration-200 ease-out" enter-from-class="opacity-0 max-h-0" enter-to-class="opacity-100 max-h-40" leave-active-class="transition-all duration-200 ease-in" leave-from-class="opacity-100 max-h-40" leave-to-class="opacity-0 max-h-0">
                <div v-show="expandedGroups.includes(item.id)" class="submenu mt-1 space-y-1 overflow-hidden">
                  <router-link v-for="child in item.children" :key="child.id" :to="child.to" :class="['nav-subitem group flex items-center pl-11 pr-3 py-2 text-sm font-medium rounded-lg transition-colors duration-200', isActiveRoute(child.to) ? 'bg-blue-50 text-blue-700 border-r-2 border-blue-700' : 'text-gray-600 hover:bg-gray-50 hover:text-gray-900']">
                    <Icon :icon="child.icon" :class="['mr-3 h-4 w-4 transition-colors duration-200', isActiveRoute(child.to) ? 'text-blue-700' : 'text-gray-400 group-hover:text-gray-500']"/>
                    <span class="flex-1">{{ child.label }}</span>
                    <span v-if="child.badge" :class="['inline-flex items-center justify-center px-2 py-1 text-xs font-bold leading-none rounded-full', child.badgeClass || 'bg-red-100 text-red-600']">
                      {{ child.badge }}
                    </span>
                  </router-link>
                </div>
              </transition>
            </div>
          </div>
        </nav>

        <!-- Sidebar Footer -->
        <div class="sidebar-footer mt-auto px-4 py-4 border-t border-gray-200 space-y-2 flex-shrink-0">
          <button 
            @click="goToHome"
            class="w-full flex items-center justify-center px-4 py-2 text-sm font-medium text-gray-700 bg-gray-50 border border-gray-300 rounded-lg hover:bg-gray-100 transition-colors duration-200"
          >
            <Icon icon="mdi:home" class="mr-2 h-4 w-4" />
            Kembali ke Home
          </button>
          <button 
            @click="logout"
            class="w-full flex items-center justify-center px-4 py-2 text-sm font-medium text-white bg-red-600 border border-transparent rounded-lg hover:bg-red-700 transition-colors duration-200"
          >
            <Icon icon="mdi:logout" class="mr-2 h-4 w-4" />
            Keluar
          </button>
        </div>
      </div>
    </aside>

    <!-- Sidebar Overlay for Mobile -->
    <div 
      v-show="sidebarOpen" 
      @click="closeSidebar"
      class="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
    ></div>

    <!-- Main Content -->
    <main :class="['main-content flex-1 flex flex-col min-h-screen transition-all duration-300']">
      <!-- Top Header -->
      <header class="main-header bg-white shadow-sm border-b border-gray-200 sticky top-0 z-30">
        <div class="flex items-center justify-between h-16 px-4 sm:px-6 lg:px-8">
          <!-- Left side -->
          <div class="flex items-center">
            <!-- Mobile menu button -->
            <button 
              @click="toggleSidebar"
              class="lg:hidden mr-3 p-2 rounded-md text-gray-400 hover:text-gray-600 hover:bg-gray-100"
            >
              <Icon icon="mdi:menu" class="h-5 w-5" />
            </button>

            <!-- Breadcrumb -->
            <nav class="breadcrumb hidden sm:flex" aria-label="Breadcrumb">
              <ol class="flex items-center space-x-2 text-sm">
                <li>
                  <div class="flex items-center">
                    <Icon icon="mdi:home" class="h-4 w-4 text-gray-400" />
                    <span class="ml-1 text-gray-500">Dashboard</span>
                  </div>
                </li>
                <li v-if="currentPage !== 'Dashboard'">
                  <div class="flex items-center">
                    <Icon icon="mdi:chevron-right" class="h-4 w-4 text-gray-400 mx-1" />
                    <span class="text-gray-900 font-medium">{{ currentPage }}</span>
                  </div>
                </li>
              </ol>
            </nav>
          </div>

          <!-- Right side -->
          <div class="flex items-center space-x-4">
            <!-- Notifications -->
            <div class="relative">
              <button 
                @click="toggleNotifications"
                class="p-2 text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded-full transition-colors duration-200"
              >
                <Icon icon="mdi:bell" class="h-5 w-5" />
                <span 
                  v-if="unreadNotifications > 0" 
                  class="absolute -top-1 -right-1 h-4 w-4 bg-red-500 text-white text-xs font-bold rounded-full flex items-center justify-center"
                >
                  {{ unreadNotifications }}
                </span>
              </button>

              <!-- Notifications Dropdown -->
              <transition name="dropdown">
                <div 
                  v-show="notificationsOpen" 
                  class="absolute right-0 mt-2 w-80 bg-white rounded-lg shadow-lg border border-gray-200 py-2 z-50"
                >
                  <div class="px-4 py-2 border-b border-gray-200">
                    <h3 class="text-sm font-semibold text-gray-900">Notifikasi</h3>
                  </div>
                  <div class="max-h-64 overflow-y-auto">
                    <div v-for="notification in notifications" :key="notification.id" class="px-4 py-3 hover:bg-gray-50">
                      <p class="text-sm text-gray-900">{{ notification.message }}</p>
                      <p class="text-xs text-gray-500 mt-1">{{ notification.time }}</p>
                    </div>
                  </div>
                </div>
              </transition>
            </div>

            <!-- User Menu -->
            <div class="relative">
              <button 
                @click="toggleUserMenu"
                class="flex items-center space-x-2 p-2 rounded-lg hover:bg-gray-100 transition-colors duration-200"
              >
                <div class="w-8 h-8 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
                  <span class="text-white font-semibold text-sm">{{ userInitials }}</span>
                </div>
                <Icon icon="mdi:chevron-down" class="h-4 w-4 text-gray-500" />
              </button>

              <!-- User Dropdown -->
              <transition name="dropdown">
                <div 
                  v-show="userMenuOpen" 
                  class="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border border-gray-200 py-2 z-50"
                >
                  <div class="px-4 py-2 border-b border-gray-200">
                    <p class="text-sm font-semibold text-gray-900">{{ currentUser.name }}</p>
                    <p class="text-xs text-gray-500">{{ maskEmail(currentUser.email) }}</p>
                  </div>
                  <button @click="navigateToProfile" class="w-full flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50">
                    <Icon icon="mdi:account" class="mr-3 h-4 w-4" />
                    Profil
                  </button>
                  <button @click="navigateToSettings" class="w-full flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50">
                    <Icon icon="mdi:cog" class="mr-3 h-4 w-4" />
                    Pengaturan
                  </button>
                  <div class="border-t border-gray-200 mt-2">
