</template>

<script>
import { Icon } from "@iconify/vue2";
import { mapActions } from 'vuex';

export default {
  name: 'DashboardView',
  components: {
    Icon
  },
  data() {
    return {
      sidebarOpen: false,
      userMenuOpen: false,
      notificationsOpen: false,
      expandedGroups: ['reports'],
      unreadNotifications: 3,
      
      // Sample notifications
      notifications: [
        {
          id: 1,
          message: 'Laporan baru telah diterima',
          time: '2 jam yang lalu'
        },
        {
          id: 2,
          message: 'Status laporan telah diperbarui',
          time: '4 jam yang lalu'
        },
        {
          id: 3,
          message: 'Reminder: Review laporan pending',
          time: '1 hari yang lalu'
        }
      ],
      
      // Menu items configuration based on user roles
      menuItems: [
        {
          id: 'dashboard',
          label: 'Dashboard',
          icon: 'mdi:view-dashboard',
          to: '/dashboard',
          roles: ['user', 'admin']
        },
        // User role menu items
        {
          id: 'my-reports',
          label: 'Laporan',
          icon: 'mdi:file-document',
          to: '/dashboard/my-reports',
          roles: ['user']
        },
        {
          id: 'evidence',
          label: 'Evidence',
          icon: 'mdi:file-image',
          to: '/dashboard/evidence',
          roles: ['user']
        },
        // Admin role menu items
        {
          id: 'all-reports',
          label: 'Kelola Laporan',
          icon: 'mdi:file-document-multiple',
          to: '/dashboard/all-reports',
          roles: ['admin']
        },
        {
          id: 'manage-evidence',
          label: 'Kelola Evidence',
          icon: 'mdi:shield-check',
          to: '/dashboard/manage-evidence',
          roles: ['admin']
        },
        {
          id: 'manage-users',
          label: 'Kelola User',
          icon: 'mdi:account-multiple',
          to: '/dashboard/manage-users',
          roles: ['admin']
        },
        {
          id: 'profile',
          label: 'Profil',
          icon: 'mdi:account',
          to: '/dashboard/profile',
          roles: ['user', 'admin']
        }
      ]
    }
  },
  computed: {
    // Get user data from store, fallback to local data if not available
    currentUser() {
      return this.$store.getters.user || {
        name: 'John Doe',
        email: 'john.doe@example.com',
        role: 'admin'
      };
    },
    
    userInitials() {
      return this.currentUser.name
        .split(' ')
        .map(name => name.charAt(0))
        .join('')
        .toUpperCase();
    },
    
    filteredMenuItems() {
      return this.menuItems.filter(item => {
        if (item.roles.includes(this.currentUser.role)) {
          if (item.children) {
            item.children = item.children.filter(child => 
              child.roles.includes(this.currentUser.role)
            );
            return item.children.length > 0;
          }
          return true;
        }
        return false;
      });
    },
    
    currentPage() {
      const route = this.$route.path;
      const menuItem = this.findMenuItemByRoute(route);
      return menuItem ? menuItem.label : 'Dashboard';
    }
  },
  methods: {
    ...mapActions(['fetchUserProfile']),
    
    // Function to mask email for privacy
    maskEmail(email) {
      if (!email || typeof email !== 'string') return '';
      
      const [localPart, domain] = email.split('@');
      if (!localPart || !domain) return email;
      
      // Show first character and last character of local part, mask the middle
      let maskedLocalPart;
      if (localPart.length <= 2) {
        maskedLocalPart = '*'.repeat(localPart.length);
      } else if (localPart.length <= 4) {
        maskedLocalPart = localPart[0] + '*'.repeat(localPart.length - 2) + localPart[localPart.length - 1];
      } else {
        maskedLocalPart = localPart[0] + '*'.repeat(localPart.length - 2) + localPart[localPart.length - 1];
      }
      
      return `${maskedLocalPart}@${domain}`;
    },
    
    toggleSidebar() {
      this.sidebarOpen = !this.sidebarOpen;
    },
    
    closeSidebar() {
      this.sidebarOpen = false;
    },
    
    toggleUserMenu() {
      this.userMenuOpen = !this.userMenuOpen;
      this.notificationsOpen = false;
    },
    
    toggleNotifications() {
      this.notificationsOpen = !this.notificationsOpen;
      this.userMenuOpen = false;
    },
    
    toggleMenuGroup(groupId) {
      const index = this.expandedGroups.indexOf(groupId);
      if (index > -1) {
        this.expandedGroups.splice(index, 1);
      } else {
        this.expandedGroups.push(groupId);
      }
    },
    
    isActiveRoute(route) {
      return this.$route.path === route;
    },
    
    hasActiveChild(menuItem) {
      if (!menuItem.children) return false;
      return menuItem.children.some(child => this.isActiveRoute(child.to));
    },
    
    findMenuItemByRoute(route) {
      for (const item of this.menuItems) {
        if (item.to === route) return item;
        if (item.children) {
          const child = item.children.find(child => child.to === route);
          if (child) return child;
        }
      }
      return null;
    },
    
    logout() {
      // Call logout action from store
      this.$store.dispatch('logout').then(() => {
        // Remove body class
        document.body.classList.remove('dashboard-page');
        // Redirect to home page
        this.$router.push('/');
        // Show success message
        this.$message.success('Berhasil logout');
      });
    },
    
    goToHome() {
      // Remove body class
      document.body.classList.remove('dashboard-page');
      // Navigate to home page
      this.$router.push('/');
    },
    
    navigateToProfile() {
      this.$router.push('/dashboard/profile');
      this.userMenuOpen = false;
    },
    
    navigateToSettings() {
      this.$router.push('/dashboard/settings');
      this.userMenuOpen = false;
    },
    
    async loadUserProfile() {
      try {
        console.log('Loading user profile...');
        await this.fetchUserProfile();
        console.log('User profile loaded successfully');
      } catch (error) {
        console.error('Failed to load user profile:', error);
        // Don't show error message for profile fetch failure
        // User can still use the dashboard with fallback data
      }
    }
  },
  
  mounted() {
    // Add dashboard class to body to hide navbar/footer
    document.body.classList.add('dashboard-page');
    
    // Fetch user profile from API
    this.loadUserProfile();
    
    // Close dropdowns when clicking outside
    document.addEventListener('click', (e) => {
      if (!e.target.closest('.relative')) {
        this.userMenuOpen = false;
        this.notificationsOpen = false;
      }
    });
    
    // Close sidebar on mobile when route changes
    this.$router.afterEach(() => {
      this.sidebarOpen = false;
    });
    
    // Handle window resize
    window.addEventListener('resize', () => {
      if (window.innerWidth >= 1024) {
        this.sidebarOpen = false;
      }
    });
  },
  
  beforeDestroy() {
    // Remove dashboard class when leaving dashboard
    document.body.classList.remove('dashboard-page');
  }
}
</script>

